import jwtDecode from "jwt-decode";
import HttpService from "./HttpService";
import config from "../config.json";

const authApi = config.ApiUrl + "/login";
const usersApi = config.ApiUrl + "/users";

export async function login(user) {
  const { data } = await HttpService.post(authApi, {
    username: user.email,
    password: user.password,
  });
  const { token: jwt } = data;

  localStorage.setItem("token", jwt);
}

export function logout() {
  localStorage.removeItem("token");
}

export function getCurrentUser() {
  try {
    const jwt = localStorage.getItem("token");
    const user = jwtDecode(jwt);
    return user;
  } catch (error) {
    return null;
  }
}

HttpService.setJwt(getJwt());

export function getJwt() {
  return localStorage.getItem("token");
}

function getUsers() {
  return HttpService.get(usersApi);
}

export function acceptUser(user) {
  return HttpService.put(usersApi + "/" + user._id, { isAccept: true });
}
export function rejectUser(user) {
  return HttpService.put(usersApi + "/" + user._id, { isAccept: false });
}

export default {
  login,

  logout,
  getCurrentUser,
  getUsers,
  getJwt,
  acceptUser,
  rejectUser,
};
