import React, { Component } from "react";
import {
  FaTimes,
  FaPenAlt,
  FaTimesCircle,
  FaCheckCircle,
} from "react-icons/fa";
import { InfoContainer, InfoWrapper, InfoTable } from "./UsersElements";
import authService from "../../services/authService";
import { toast } from "react-toastify";

export default class Users extends Component {
  state = {
    users: [],
  };
  async populateUsers() {
    try {
      const { data: users } = await authService.getUsers();
      this.setState({ users });
    } catch (error) {
      if (error) {
        toast.error("An unexpected error occured");
      }
    }
  }
  componentDidMount() {
    this.populateUsers();
  }

  //   update user is accept to true
  handleUpdate = async (user) => {
    await authService.acceptUser(user);
    toast("Accepted");
    window.location = "/users";
  };
  //   update user is accept to true
  handleReject = async (user) => {
    await authService.rejectUser(user);
    toast("Rejected");
    window.location = "/users";
  };

  render() {
    return (
      <>
        <InfoContainer>
          <InfoWrapper>
            <InfoTable className="table">
              <thead>
                <tr>
                  <th>Name</th>
                  <th>Id No</th>
                  <th>Status</th>
                  <th>Accept</th>
                  <th>Reject</th>
                </tr>
              </thead>
              <tbody>
                {this.state.users.map((user) => (
                  <tr key={user._id}>
                    <td>{user.name}</td>
                    <td>{user._id.substring(0, 10)}</td>
                    <td style={{ fontSize: "1.3rem" }}>
                      {user.isAccept ? (
                        <FaCheckCircle style={{ color: "yellow" }} />
                      ) : (
                        <FaTimesCircle style={{ color: "red" }} />
                      )}
                    </td>

                    <td>
                      <button
                        onClick={() => this.handleUpdate(user)}
                        className="btn btn-secondary btn-sm m-2"
                      >
                        Accept <FaPenAlt />
                      </button>
                    </td>
                    <td>
                      <button
                        onClick={() => this.handleReject(user)}
                        className="btn btn-danger btn-sm "
                      >
                        Reject <FaTimes />
                      </button>
                    </td>
                  </tr>
                ))}
              </tbody>
            </InfoTable>
          </InfoWrapper>
        </InfoContainer>
      </>
    );
  }
}
