import React, { useState, useEffect, useCallback } from "react";
import { FaMoon, FaBell } from "react-icons/fa";
import { animateScroll as scroll } from "react-scroll/modules";

import { getCurrentUser as user } from "../../services/authService";

import {
  Nav,
  NavLogo,
  ServiceLink,
  NavbarContainer,
  MobileIcon,
  NavMenu,
  NavItem,
  NavLinks,
  NavBtn,
  Burger,
  NavBtnLink,
} from "./NavbarElements";
const Navbar = ({ toggle, isOpen }) => {
  const [scrollNav, setScrollNav] = useState(false);

  const changeNav = useCallback(() => {
    if (window.scrollY >= 80) {
      setScrollNav(true);
    } else {
      setScrollNav(false);
    }
  }, []);
  useEffect(() => {
    window.addEventListener("scroll", changeNav);

    return () => window.removeEventListener("scroll", changeNav);
  }, [changeNav]);

  const toggleHome = () => {
    scroll.scrollToTop();
  };

  return (
    <>
      <MobileIcon onClick={toggle}>
        {isOpen ? <Burger className="open" /> : <Burger />}
      </MobileIcon>
      {!user() && (
        <Nav scrollNav={scrollNav}>
          <NavbarContainer>
            <NavLogo to="/" onClick={toggleHome}>
              <FaMoon />
            </NavLogo>

            <NavMenu>
              <NavItem>
                <NavLinks
                  to="about"
                  smooth={true}
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={-80}
                >
                  About
                </NavLinks>
              </NavItem>

              <NavItem>
                <NavLinks
                  to="signup"
                  smooth={true}
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={-80}
                >
                  Sign Up
                </NavLinks>
              </NavItem>
            </NavMenu>
            <NavBtn onClick={toggle}>
              <NavBtnLink to="/signin">Sign In</NavBtnLink>
            </NavBtn>
          </NavbarContainer>
        </Nav>
      )}
      {user() && (
        <Nav scrollNav={scrollNav}>
          <NavbarContainer>
            <NavLogo to="/" onClick={toggleHome}>
              <FaMoon />
            </NavLogo>

            <NavMenu>
              <NavItem>
                <NavLinks
                  to="discover"
                  smooth={true}
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={-80}
                >
                  Discover
                </NavLinks>
              </NavItem>
              <NavItem>
                <NavLinks
                  to="services"
                  smooth={true}
                  duration={500}
                  spy={true}
                  exact="true"
                  offset={-80}
                >
                  Services
                </NavLinks>
              </NavItem>
              <NavItem>
                <ServiceLink to="/profile">
                  <FaBell></FaBell>
                  {user().isAccept ? (
                    <span className="badge badge-pill badge-danger">1</span>
                  ) : null}
                </ServiceLink>
              </NavItem>
              {user().isAdmin === true ? (
                <NavItem>
                  <ServiceLink
                    to="/users"
                    smooth={true}
                    duration={500}
                    spy={true}
                    exact="true"
                    offset={-80}
                  >
                    Manage users
                  </ServiceLink>
                </NavItem>
              ) : null}
            </NavMenu>
            <NavBtn onClick={toggle}>
              <NavBtnLink to="/signout">Sign out</NavBtnLink>
            </NavBtn>
          </NavbarContainer>
        </Nav>
      )}
    </>
  );
};

export default Navbar;
